.features {
  margin: 0px auto;
  padding: 60px 0px 125px 0px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.features__img {
  margin: -60px 0px 0px 0px;
  width: 550px;
  height: 550px;
  object-fit: contain;
  object-position: center;
  pointer-events: none;
}
.features__info {
  max-width: 500px;
  margin-left: 60px;
}
.features__title {
  margin: 0px;
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 50px;
  color: var(--title-color);
}
.features__subtitle {
  margin: 20px 0px 0px 0px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  color: var(--subtitle-color);
}
.features__subtitle_weight_bold {
  font-weight: bold;
}